/**
 * WordPress dependencies
 */
import apiFetch from '@safe-wordpress/api-fetch';
import { dispatch } from '@safe-wordpress/data';

/**
 * External dependencies
 */
import type { MediaId } from '@nab/types';

/**
 * Internal dependencies
 */
import { STORE_NAME as NAB_DATA } from '../../config';

export async function getMediaUrl( id: MediaId ): Promise< void > {
	if ( ! id ) {
		return;
	} //end if

	try {
		// eslint-disable-next-line camelcase
		type Media = { readonly source_url: string };
		const media = await apiFetch< Media >( {
			path: `/wp/v2/media/${ id }`,
		} );
		dispatch( NAB_DATA ).receiveMediaUrl( id, media.source_url );
	} catch ( error ) {
		const message = error instanceof Error ? error.message : 'unknown';
		// eslint-disable-next-line
		console.warn( `Error while retrieving media. Error: ${ message }` );
	} //end try
} //end getMediaUrl()
